@font-face {
  font-family: 'martin-web';
  src: url('martin-web/MARTIN-Regular.eot'); /* IE9 Compat Modes */
  src: url('martin-web/MARTIN-Regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('martin-web/MARTIN-Regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('martin-web/MARTIN-Regular.woff') format('woff');
}

html, body {
  padding:0px;
  margin:0px;
  
}

* {
  box-sizing: border-box;
}

body { 
  background-color:black;
  color:white;
  font-family:Georgia;
}

h1,h2 { 
  font-family:"martin-web", sans-serif;
  text-align:center;
  text-transform:uppercase;
  font-weight:500;
}

h1 {
  font-size:160px;
  margin-bottom:0;
  line-height: .7em;
}

h2 {
  font-family:Georgia;
  margin-bottom:150px;
  margin-top:0;
}

.namelist {
  max-width:1040px;
  margin:0 auto;


  &__entry {
    position:relative;
    min-height:20px;
  }

  &__line {
    position:absolute;
    left:50%;
    top:0px;
    height:100%;
    width:3px;
    background-color:white;
  }

  &__dot { 
    position:absolute;
    background-color:black;
    border-radius:50%;
    width:11px;
    height:11px;
    top:3px;
    left:-5px;
    border:1px solid white;
  }

  &__date {
    position:absolute;
    right: 50%;
    padding-right:10px;
    color:white;
    font-size:16px;
  }


  &__details {
    position:relative;
    left: 50%;
    padding-left:16px;
    color:white;
    font-size:14px;
    padding-bottom:30px;
    top:-5px;
    max-width:50%;
  }

  &__name { 
    font-size:24px;
  }

  &__image {
    width:400px;
    height:140px;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: left top;

  }

}

@media screen and (max-width:600px) {
  
  .namelist {

  &__entry {
  }

  &__line {
    left:30%;
  }

  &__date {
    right: 70%;
  }
    
  &__details { 
    left: 30%;
  }



}

  
  
  
}